@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&display=swap);
body{margin:0;font-family:Merriweather, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;background:#F6F3F3;color:#343030}

.app{display:flex;flex-direction:column;height:100vh}main{flex:1 1}.logo{margin-right:24px}h1{font-weight:700;font-size:24px;line-height:150%;margin:0}h2{font-weight:normal;font-size:18px;line-height:150%;margin:0}.btn{font-family:Merriweather;cursor:pointer;font-size:14px;font-weight:700;background:#382E2B;color:#F4F6F9;height:100%;width:118px;display:flex;justify-content:center;align-items:center;border:none;position:relative}.btn:disabled{background:#C1B4B1;cursor:not-allowed;color:transparent}.btn:disabled img{opacity:0}.btn:disabled:after{content:"";height:20px;width:20px;background:url(https://i.gifer.com/VAyR.gif) no-repeat;background-size:cover;position:absolute;top:0;left:0;right:0;bottom:0;margin:auto}.btn.in_the_cart{background:#5B3A32}.btn.in_the_cart img{margin-right:8px}.btn:hover{background:#776763}

.header{padding:24px calc(50% - 1216px / 2);height:96px;box-sizing:border-box;border-bottom:1px solid #E1E1E1;display:flex;justify-content:space-between}.header__left{display:flex;align-items:center}

.menu{display:flex}.menu__item{cursor:pointer;margin:24px;font-size:14px;line-height:21px}

.search{width:416px;height:100%;display:flex}.search .search__input{font-family:Merriweather;height:100%;box-sizing:border-box;outline:none}.search .search__input.search__text{max-width:293px;width:100%;padding:0 14px;box-sizing:border-box;border:1px solid #e1e1e1;border-right:0;color:#343030;font-size:14px}.search .search__input.search__text:focus{border:1px solid #B5B5B5;border-right:0}.search .search__input.search__text::-webkit-input-placeholder{color:#9F9F9F}.search .search__input.search__text:-ms-input-placeholder{color:#9F9F9F}.search .search__input.search__text::-ms-input-placeholder{color:#9F9F9F}.search .search__input.search__text::placeholder{color:#9F9F9F}.search .search__input.search__btn{width:122px}

.footer{padding:24px calc(50% - 1214px / 2);height:96px;box-sizing:border-box;background:#ECEAEA;display:flex;justify-content:space-between}.footer__left{display:flex;align-items:center}.footer__contact{height:100%;display:flex;align-items:center}.footer__contact .contact__item{font-size:14px;display:flex;align-items:center;margin-left:48px}.footer__contact .contact__item img{margin-right:11px}

.showcase{margin:45px 0;padding:0 calc(50% - 1216px / 2)}.product_cards__wrapper{display:flex;flex-wrap:wrap;justify-content:space-between;align-content:stretch;margin-top:40px}

.product_card{margin-bottom:20px;border:1px solid #e1e1e1;box-sizing:border-box;width:280px}.product_card.sold{opacity:50%}.product_card.sold .product_card__wrapper{font-weight:700}.product_card__img{width:100%;height:156px}.product_card__img img{width:100%;height:100%;object-fit:cover}.product_card__info{box-sizing:border-box;padding:21px 23px 24px}.product_card__wrapper{margin-top:22px;height:48px;display:flex;align-items:center;justify-content:space-between}.product_card__price{display:flex;height:100%;flex-direction:column;justify-content:center;line-height:150%}.product_card__old_price{font-size:14px;text-decoration:line-through;color:#a0a0a0}

